@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    font-size: 14px;
    line-height: 16px;
    .header{
      &__headline{
        .container{
          .row{
            .col-md-12{
              p{
                font-size: 8.3em;
              }
            }
            .col-md-6{
              .logo{
                a{
                  width: 285px;
                }
                div{
                  p{
                    &:nth-child(1){
                      font-size: 1em;
                    }
                    &:nth-child(2){
                      font-size: 1.24em;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    font-size: 16px;
    line-height: 18px;
    .header{
      &__headline{
        height: 105px;
        .container{
          .row{
            .col-sm-12{
              p{
                font-size: 5.6em;
                line-height: 105px;
              }
            }
            .col-sm-6{
              .logo,
              .search{
                height: 105px;
                top: -105px;
              }
              .logo{
                a{
                  height: 105px;
                }
              }
            }
          }
        }
      }
      &__mainNav{
        .container{
          .row{
            .col-sm-12{
              ul{
                li{
                  a{
                    span{
                      &:nth-child(2){
                        font-size: .688em;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px), screen and (max-device-width: 767px){
  body{
    .header{
      &__nav{
        .container{
          .row{
            .col-xs-1{
              .nav{
                display: none;
              }
              .mobile__nav{
                display: block;
              }
            }
          }
        }
      }
      &__headline{
        .container{
          .row{
            .col-xs-12{
              p{
                font-size: 5em;
              }
            }
            .col-sm-6{
              .search{
                display: none;
              }
            }
          }
        }
      }
      &__mainNav{
        .container{
          .row{
            .col-xs-12{
              .mainNav{
                display: none;
              }
              .mobile__mainNav{
                display: flex;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 690px), screen and (max-device-width: 690px){
  body{
    .header{
      &__headline{
        .container{
          .row{
            .col-xs-12{
              p{
                display: none;
              }
              .logo{
                position: unset;
                div{
                  p{
                    display: block;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}